import React from 'react';
import { styled } from '@mui/system';

const Bubble = styled('div')(({ rect, arrowDir }) => {
  if (!rect) return {};

  const width = 280;
  let top = rect.top - 140;
  let left = rect.left + rect.width / 2 - width / 2;

  // 若泡泡超出左側
  if (left < 16) left = 16;

  // 超出右側
  if (left + width > window.innerWidth - 16) left = window.innerWidth - width - 16;

  // 若泡泡在上方放不下 → 移到 target 下方
  if (top < 40) top = rect.bottom + 20;

  return {
    position: 'fixed',
    top,
    left,
    width,
    background: '#fff',
    padding: '16px 18px',
    borderRadius: '12px',
    boxShadow: '0 4px 18px rgba(0,0,0,0.25)',
    zIndex: 10000,
    animation: 'fadeIn 0.25s ease-out',
  };
});

// 箭頭
const Arrow = styled('div')(({ rect, arrowDir }) => {
  if (!rect) return {};

  let top, left;

  if (arrowDir === 'up') {
    top = rect.top - 25;
    left = rect.left + rect.width / 2 - 10;
  } else {
    top = rect.bottom + 5;
    left = rect.left + rect.width / 2 - 10;
  }

  return {
    position: 'fixed',
    top,
    left,
    width: 0,
    height: 0,
    borderLeft: '10px solid transparent',
    borderRight: '10px solid transparent',
    borderTop: arrowDir === 'up' ? '10px solid #fff' : 'none',
    borderBottom: arrowDir === 'down' ? '10px solid #fff' : 'none',
    zIndex: 10001,
  };
});

export default function BubbleStep({
  rect,
  arrowDir,
  title,
  text,
  onNext,
  onPrev,
  isLast,
  hasPrev,
}) {
  if (!rect) return null;

  return (
    <>
      <Bubble rect={rect} arrowDir={arrowDir}>
        <strong>{title}</strong>
        <p style={{ margin: '6px 0', fontSize: 14, color: '#444' }}>{text}</p>

        <div style={{ marginTop: 12, display: 'flex', gap: 8 }}>
          {hasPrev && (
            <button
              style={{
                flex: 1,
                padding: '10px',
                borderRadius: 8,
                border: '1px solid #ccc',
                background: '#f5f5f5',
              }}
              onClick={onPrev}
            >
              上一步
            </button>
          )}

          <button
            style={{
              flex: 1,
              padding: '10px',
              borderRadius: 8,
              background: '#6A4DFF',
              color: '#fff',
              border: 'none',
            }}
            onClick={onNext}
          >
            {isLast ? '完成' : '下一步'}
          </button>
        </div>
      </Bubble>

      <Arrow rect={rect} arrowDir={arrowDir} />
    </>
  );
}
