import React, { useEffect, useState } from 'react';
import { styled } from '@mui/system';
import BubbleStep from './BubbleStep';
import useTourPosition from './useTourPosition';

// 遮罩
const Mask = styled('div')({
  position: 'fixed',
  inset: 0,
  background: 'rgba(0,0,0,0.55)',
  backdropFilter: 'blur(2px)',
  zIndex: 9998,
});

// 底部圓點
const StepDots = styled('div')({
  display: 'flex',
  justifyContent: 'center',
  marginTop: 16,
});

const Dot = styled('div')(({ active }) => ({
  width: 8,
  height: 8,
  borderRadius: '50%',
  margin: '0 5px',
  background: active ? '#6A4DFF' : '#bbb',
  transition: '0.25s',
}));

export default function BubbleTour({ open, steps, onClose }) {
  const [index, setIndex] = useState(0);
  const { rect, arrowDir } = useTourPosition(steps[index]?.target, open);

  useEffect(() => {
    if (open) setIndex(0);
  }, [open]);

  if (!open) return null;
  const step = steps[index];

  const next = () => {
    if (index < steps.length - 1) {
      setIndex(index + 1);
    } else {
      onClose();
    }
  };

  const prev = () => {
    if (index > 0) setIndex(index - 1);
  };

  return (
    <>
      <Mask onClick={onClose} />

      <BubbleStep
        rect={rect}
        arrowDir={arrowDir}
        title={step.title}
        text={step.text}
        onNext={next}
        onPrev={prev}
        hasPrev={index > 0}
        isLast={index === steps.length - 1}
      />

      {/* Step dots */}
      <StepDots>
        {steps.map((_, i) => (
          <Dot key={i} active={i === index} />
        ))}
      </StepDots>
    </>
  );
}
